@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
*{
  margin:0px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.css-i4bv87-MuiSvgIcon-root {
  margin: 0px 10px;
}
.App{
  display:grid;
  grid-template-columns: 300px auto 50px;
}
.loginwrap img{
  width:100px;
}
.loginwrap{
  background: rgb(249, 250, 248);
  padding: 20px;
  width:500px;
  margin: 0px auto;
  display:flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
 
}
.loginwrap button{
  width:100%;
  background: #2381fa;
  padding: 10px 20px;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 16px;
  border-radius: 10px;
  margin-top: 20px;
  font-size: 20px;
}
.head{
  margin-top: 5px;
  font-family: 'Lobster', cursive;
  font-size: 26px;
  font-weight: 500;
}

.header{
    display:grid;
    grid-template-columns: 300px auto 200px;
    align-items: flex;
    height: 80px;
    width: 100%;
    /* background-color: chartreuse; */
    padding-top: 20px;
    padding-left: 1%;
    padding-right: 25px;    
    border-bottom: 1px solid light gray;
    margin-bottom: 15px;
    position: relative;
}
.header__logo{
    /* background-color:turquoise; */
    padding-left: 6%;
    vertical-align: middle;
}
.header__logo img{
    width:40px;
    padding-top: 8px;
}
.header__logo span{
    font-size: 31px;   /*previous value 36px*/
    color:gray;
    padding-left: 15px;
    vertical-align: top;
}
.header__search{
    display:flex;
    align-items: center;
    width: 55%;
    height: 50px;
    background-color: whitesmoke;
    padding:12px;
    border-radius: 10px;
    margin-left: 20%;
    margin-right: 10%;
    min-width: 500px;
    /* background-color: tomato; */
}
.header__search button{
    background-color: transparent;
    border: transparent;
}
.AlignIcon{
    margin-left: 10%;
    position: inherit;
}
.header__search input{
    background-color: transparent;
    border:0;
    outline:0;
    flex:1 1;
}
.header__icons{
    display:flex;
    align-self: start;
    align-items: center;
    /* background-color: tomato; */
}

.header__icons span{
    display:flex;
    align-items: center;
    /* background-color: tomato; */
    margin-left: 10px;
}
.header__icons span:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
}

#SettingsIcon{
    display:flex;
    align-items: center;
    margin-left: 10px;
}

#SettingsIcon:hover{
    -webkit-transform: rotate(45deg) scale(1.2);
            transform: rotate(45deg) scale(1.2);
    transition-duration: 0.5s;
    cursor: pointer;
}

#Avatar{
    display:flex;
    align-items: center;
    margin-left: 25px;
    margin-right: 6%;
}

#Avatar:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition-duration: 0.5s;
}
.sidebar{
    margin-top: 10px;
    /* background-color: chartreuse; */
    height: 826px;
    align-content: center;
}
.sidebar_btn img{
    width:24px;
    height:24px;
}
.sidebar_btn button{
    background-color: transparent;
    border: 2px solid rgb(74, 120, 247);
    display: flex;
    width: 200px;
    height: 50px;
    align-items: center;
    border-radius: 40px;
    padding-left: 25px;
    margin-left: 25px;
    margin-bottom: 25px;
    margin-top: 15px;
}
.sidebar_btn button span{
    font-size: 16px;
    margin-right: 20px;
    margin-left: 15px;
    font-weight: 400;
}
.sidebar_btn button:hover{
    font-size: 18px;
    margin-right: 20px;
    margin-left: 25px;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    transition: all;
}
.sidebar_btn img:hover {
    cursor: pointer;
    width:30px;
}
.sidebar__option{
    margin-top: 10px;
}
.sidebar__option{
    display:flex;
    align-items: center;
    margin:15px;
    margin-left: 15px;
    padding:8px 20px;
    border-radius: 0px 20px 20px 0px;
}
.sidebar__option:hover{
    background-color: lightgray;
    cursor: pointer;
}
.sidebar__option span{
    margin-left: 15px;
    font-size: 13px;
    font-weight: 500;
    color:rgb(78,78,78);
}
.sidebaroptionActive{
    background-color: whitesmoke;
}
.sidebaroptionActive span{
    color: rgb(86, 88, 214);
}
.progress_bar{
    margin-left:5px;
    padding: 0px 20px;
}
.progress_bar span{
    display: block;
    font-size: 13px;
    color:black;
    text-align: left;
}

.modal_pop{
    top:50%;
    background-color: #fff;
    width: 500px;
    margin:0px auto;
    position: relative;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding: 10px;
    border-radius: 10px;
}
.modalHeading{
    text-align: center;
    border-bottom: 1px solid lightgray;
    height: 40px;
}
input.post_submit{
    width: 100%;
    background:#2381fa;
    padding: 10px 20px;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 16px;
    border: 0;
    outline:0;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
}
.uploading{
    text-align: center;
    color:rgb(14, 13, 13);
    background-color: greenyellow;
    width:100%;
    padding:10px 20px;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 16px;
    border: 0;
    outline:0;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;   
    font-weight:500;
}
.logout{
    background-color: #2381fa;
    font-size: 20px;
    border-radius: 10px;
    width:50%;
    padding: 5px 5px;
    text-align: center;
    color:#fff;
    margin:20px 40px;

}
.headtext{
    font-size: 13;
    display: flex;
    margin-left:120px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.data{
    flex:1 1;
    padding: 10px 0px 0px 20px;
}
.data__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom:1px solid lightgray;
    height:40px;
}
.data__headerLeft{
    display: flex;
    align-items: center;
}
.data__headerRight svg{
    margin: 0px 10px;
}
.data_grid{
    display:flex;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 30px;
}
.Root-Btn{
    background-color: transparent;
    border: 2px solid rgba(0,0,0,0.5);
    display: flex;
    width: 70px;
    height: 35px;
    align-items: center;
    border-radius: 40px;
    padding: 15px;
    margin-left: 30px;
    margin-bottom: 30px;
    margin-top: 15px;
    font-size: 16px;
    font-weight: 400;
}

.Root-Btn:hover{
    background-color:rgba(0,0,0,0.5) ;
    color: #fff;
    padding: 15px;
    padding-left: 17px;
    border: none;
    z-index: 10;
}

.data_file{
text-align: center;
border: 1px solid lightgray;
margin:10px;
min-width:100px;
padding:10px 0px 0px 0px;
border-radius: 5px;
}
.data_file svg{
    background-color: transparent;
    font-size:60px;
    color:rgb(72, 73, 73);
}
.data_file p{
    border-top:1px solid #ccc;
    margin-top:5px;
    font-size: 12px;
    background-color: whitesmoke;
    padding: 10px 0px;
}
.FolderHeading{
    margin: 10px;
    margin-top: 20px;
    padding: 10px;
    font-weight: 600;
}
.folder p{
    border-top: none;
    background-color: transparent;
    text-decoration: none;
    margin-top: -5px;
    margin-bottom: 5px;
    margin-right: -5px;
    text-transform: capitalize;
    color: rgba(0,0,0,0.7);
    font-weight: 500;
    font-size: 15px;
}
.folder a{
    text-decoration: none;
}
.detailsrow{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
    padding: 10px 20px;
}
.detailsrow p{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    font-size: 13px;
    padding : 0px 10px;
}
.detailsrow p svg{
    font-size: 22px;
    margin:10px;
    color:rgb(72, 73, 73);
    background-color: transparent;

}
.Yes{
    margin-top: 10px;
    margin-left:190px;
    margin-bottom: 5px;
    padding:0px 5px;
    border-radius: 5px;
    background-color: transparent;
    border: 2px solid rgb(58, 219, 58);
}
.No{
    margin-top: 10px;
    margin-left:10px;
    margin-bottom: 5px;
    padding:0px 5px;
    border-radius: 5px;
    background-color: transparent;
    border: 2px solid rgb(248, 90, 51);
}
.delete{
    background-color: transparent;
    border-radius: 5px;
    border:1px solid rgb(249, 250, 252);
}
.link{
    margin:5px 5px;
    color:blue;
    border-radius: 5px;
    border: 1px solid blueviolet;
    width:85%;
}
.Close{
    margin-top: 10px;
    margin-left:200px;
    margin-bottom: 5px;
    padding:0px 5px;
    justify-content: space-between;
    border-radius: 5px;
    background-color: transparent;
    background-color: red;
    color:white;
    font-size: 15;
}
.data_file img{
    width:50px;
}

